<form><h3>{{ quest.id }}</h3><p>{{ quest.text }}</p><container class="form-check" v-for="(ans, index) in quest.answers"><input class="form-check-input" type="radio" name="index" :value="ans.id" v-model="answerID" @change="submit()" :id="ans.text"><label class="form-check-label" :for="ans.text">{{  ans.text }}</label></container><hr></form>